.background{
    width: 430px;
    height: 520px;
    position: absolute;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;

    .shape{
        height: 200px;
        width: 200px;
        position: absolute;
        border-radius: 50%;
    
        &:first-child {
	    	background: linear-gradient( #1845ad, #23a2f6 );
	    	left: -80px;
	    	top: -80px;
	    }
	    &:last-child {
	    	background: linear-gradient( to right, #ff512f, #f09819 );
	    	right: -70px;
	    	bottom: -80px;
	    }
    }
}
