.widget {
  display: flex;
  flex: 1;
  justify-content: space-between;
  padding: 15px;
  box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  height: 150px;

  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .title {
      font-size: 14px;
      font-weight: bold;
      color: rgb(160, 160, 160);
    }
    .counter {
      font-size: 30px;
      font-weight: 300;
      padding-left: 5px;
    }
    .Link{
      text-decoration: none;
      color:  rgb(160, 160, 160);

      .link {
        font-size: 12px;
        border-bottom: 1px solid gray;
        width: max-content;
        cursor: pointer;
      }
    }
    
    .persentage {
      display: flex;
      align-items: center;
      font-size: 14px;
    }
    .positive {
      color: green;
    }
    .negative {
      color: red;
    }
  }
  .icon {
    font-size: 30px;
    padding: 5px;
    align-self: flex-end;
    border-radius: 5px;
  }
}
